import { createSlice } from "@reduxjs/toolkit";
export const countslice=createSlice({
    name:"count",
    initialState:{
        value:1
    },
    reducers:{
        increasecount:(state)=>{
        state.value*=1.5

        },
        decreasecount:(state)=>{
            state.value/=1.5
        }
    }
})
export const{increasecount,decreasecount}=countslice.actions
export default countslice.reducer